export default {
	data() {
		return {
			userInfo:{},
		}
	},
	onShow() {
		var me = this;
		me.userInfo = uni.getStorageSync("globalUser");
	},
	methods: {
		logout(){
			uni.removeStorageSync("globalUser");
			uni.navigateBack({
				delta: 1
			});
		},
		gotoNotice(){uni.navigateTo({
				url:"../notice/notice"
			})
		},
		clearCache(){
				uni.showActionSheet({
					itemList:["确定清理缓存"],
					success(res) {
						var index = res.tapIndex;
						if(index ==0){
							try {
							    uni.clearStorageSync();
							} catch (e) {
							    // error
							}
						}
					}
				})
		}
	}
}
